export enum BotMsgType {
    CQCode, Array, Auto
}

export interface RunTimeDataElem {
    reader?: Electron.IpcRenderer | null,
    sysConfig: { [key: string]: any }
    jsonMap?: any
    botInfo: { [key: string]: any }
    loginInfo: { [key: string]: any }
    userList: (UserFriendElem & UserGroupElem)[]
    showList: (UserFriendElem & UserGroupElem)[]
    onMsgList: (UserFriendElem & UserGroupElem)[]
    systemNoticesList?: { [key: string]: any }
    chatInfo: ChatInfoElem
    pageView: {
        chatView: any,
        msgView: any
    },
    tags: {
        firstLoad: boolean
        msgType: BotMsgType
        canLoadHistory: boolean
        openSideBar: boolean,
        viewer: {
            show?: boolean,
            index: number
        },
        loginWaveTimer?: any,
        isElectron: boolean,
        platform: string | undefined,
        release: string | undefined,
        connectSsl: boolean,
        classes: any[],
        sw?: boolean,
        darkMode: boolean
    },
    watch: {                // PS: 
        newMsg: any,
        newNotice: any,
        heartbeatTime?: number,
        oldHeartbeatTime?: number,
        lastHeartbeatTime?: number
    },
    messageList: any[]
    mergeMessageList?: any[] | undefined,
    stickerCache?: any[],
    popBoxList: {                   // 
        svg?: string,                   // 
        title?: string,                 // 
        html?: string,                  //  html
        template?: any,                 //  html
        templateValue?: any,            //  props
        data?: any,                     // 
        full?: boolean,                 // 
        button?: {                      // 
            master?: boolean,               // 
            fun?: (value: any) => void,     // 
            text: string                    // 
        }[],
        allowQuickClose?: boolean,      // 
    }[]
}

export interface ChatInfoElem {
    show: BaseChatInfoElem,
    info: {
        group_info: { [key: string]: any },
        user_info: { [key: string]: any },
        me_info: { [key: string]: any },
        group_members: GroupMemberInfoElem[],
        group_files: { [key: string]: any },
        group_sub_files: { [key: string]: any },
        group_notices?: { [key: string]: any },
        now_member_info?: { [key: string]: any },
        image_list?: { index: number, message_id: string, img_url: string }[],
        jin_info: {
            list: { [key: string]: any }[],
            is_end?: boolean,
            pages: number
        }
    }
}

export interface BaseChatInfoElem {
    type: string,
    id: number,
    name: string,
    avatar: string,
    jump?: string
}

export interface UserElem {
    new_msg?: boolean,
    raw_msg?: string,
    time?: number,
    always_top?: boolean,
    message_id?: string
}

export interface UserFriendElem extends UserElem {
    group_id: number,
    group_name: string,
    py_name?: string,
    member_count?: number,
    admin_flag?: boolean
}

export interface UserGroupElem extends UserElem {
    user_id: number,
    nickname: string,
    remark: string,
    py_name?: string,
    class_id?: number,
    class_name?: string
}

export interface GroupMemberInfoElem {
    user_id: number,
    title: string,
    card: string,
    join_time: number,
    last_sent_time: number,
    level: number,
    nickname: string,
    rank: string,
    role: string,
    sex: string,
    shutup_time: number,
}

export interface SQCodeElem {
    addText: boolean,
    addTop?: boolean,
    msgObj: MsgItemElem
}

export interface MsgItemElem {
    type: string,
    [key: string]: any
}